/* Update System Styles */
.update-controls {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.update-controls .glass-button {
  min-width: 180px;
  padding: 0.75rem 1.5rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.update-controls .glass-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.update-available {
  background: linear-gradient(135deg, #10b981, #059669) !important;
  border: 1px solid rgba(16, 185, 129, 0.3) !important;
  color: white !important;
}

.install-ready {
  background: linear-gradient(135deg, #3b82f6, #2563eb) !important;
  border: 1px solid rgba(59, 130, 246, 0.3) !important;
  color: white !important;
}

.update-status {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.update-available-info {
  background: rgba(16, 185, 129, 0.1) !important;
  border: 1px solid rgba(16, 185, 129, 0.2) !important;
}

.update-error {
  background: rgba(239, 68, 68, 0.1) !important;
  border: 1px solid rgba(239, 68, 68, 0.2) !important;
  border-radius: 8px;
  padding: 1rem;
  margin-top: 1rem;
}

.error-message {
  color: #fca5a5;
  font-size: 0.9rem;
}

/* Checkbox personalizat */
.checkbox-container {
  display: flex;
  align-items: center;
  cursor: pointer;
  user-select: none;
  gap: 0.75rem;
}

.checkbox-container .glass-checkbox {
  position: relative;
  width: 20px;
  height: 20px;
  margin: 0;
  opacity: 0;
  cursor: pointer;
}

.checkmark {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
}

.checkbox-container .glass-checkbox:checked ~ .checkmark {
  background-color: #10b981;
  border-color: #10b981;
}

.checkbox-container .glass-checkbox:checked ~ .checkmark:after {
  content: "";
  position: absolute;
  left: 7px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

.checkbox-text {
  color: #e5e7eb;
  font-size: 0.9rem;
  line-height: 1.4;
}

/* Changelog Styles */
.changelog-content {
  margin-top: 1rem;
}

.release-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.release-date {
  color: #9ca3af;
  font-size: 0.9rem;
}

.prerelease-tag {
  background: rgba(251, 191, 36, 0.2);
  color: #fbbf24;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 500;
}

.changelog-text {
  max-height: 300px;
  overflow-y: auto;
  padding-right: 0.5rem;
}

.changelog-text pre {
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'Inter', sans-serif;
  font-size: 0.9rem;
  line-height: 1.6;
  color: #e5e7eb;
  margin: 0;
}

.changelog-text p {
  color: #9ca3af;
  font-style: italic;
}

/* Release History Styles */
.releases-history {
  max-height: 400px;
  overflow-y: auto;
  padding-right: 0.5rem;
}

/* Butoane de actualizare */
.update-controls {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-bottom: 1rem;
}

.download-button {
  background: rgba(59, 130, 246, 0.2) !important;
  border: 1px solid rgba(59, 130, 246, 0.3) !important;
  color: #93c5fd !important;
}

.install-button {
  background: rgba(16, 185, 129, 0.2) !important;
  border: 1px solid rgba(16, 185, 129, 0.3) !important;
  color: #86efac !important;
}

.silent-install-button {
  background: rgba(139, 92, 246, 0.2) !important;
  border: 1px solid rgba(139, 92, 246, 0.3) !important;
  color: #c4b5fd !important;
}

.download-button:hover,
.install-button:hover,
.silent-install-button:hover {
  opacity: 0.8;
  transform: none !important;
}

.download-button:disabled,
.install-button:disabled,
.silent-install-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Informaii de actualizare - Fr hover */
.update-info-display {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 1rem;
}

.version-info, .last-check-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  color: #e5e7eb;
}

.version-label, .check-label {
  font-size: 0.9rem;
  color: #9ca3af;
  font-weight: 500;
}

.version-value, .check-value {
  font-size: 0.9rem;
  color: #e5e7eb;
  font-weight: 400;
}

.update-available-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  background: rgba(16, 185, 129, 0.1);
  border: 1px solid rgba(16, 185, 129, 0.3);
  border-radius: 6px;
  color: #10b981;
  margin-bottom: 1rem;
}

.update-label {
  font-size: 0.9rem;
  color: #10b981;
  font-weight: 500;
}

.update-value {
  font-size: 0.9rem;
  color: #10b981;
  font-weight: 400;
}

.release-item {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.release-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.75rem;
}

.release-version {
  font-weight: 600;
  color: #60a5fa;
  font-size: 1rem;
}

.release-actions {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.small-button {
  padding: 0.5rem 1rem !important;
  font-size: 0.85rem !important;
  min-width: auto !important;
}

.rollback-button {
  background: rgba(239, 68, 68, 0.2) !important;
  border: 1px solid rgba(239, 68, 68, 0.3) !important;
  color: #fca5a5 !important;
}

.no-releases {
  text-align: center;
  padding: 2rem;
  color: #9ca3af;
  font-style: italic;
}

/* Responsive Design */
@media (max-width: 768px) {
  .update-controls {
    flex-direction: column;
  }
  
  .update-controls .glass-button {
    width: 100%;
    min-width: auto;
  }
  
  .release-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .release-actions {
    width: 100%;
    justify-content: flex-start;
  }
  
  .small-button {
    flex: 1;
    text-align: center;
  }
}

/* Update available styles */
.update-available {
  background: linear-gradient(135deg, #10b981, #059669) !important;
  border: 1px solid rgba(16, 185, 129, 0.3) !important;
  color: white !important;
}

/* Scrollbar styling for changelog and history */
.changelog-text::-webkit-scrollbar,
.releases-history::-webkit-scrollbar {
  width: 6px;
}

.changelog-text::-webkit-scrollbar-track,
.releases-history::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 3px;
}

.changelog-text::-webkit-scrollbar-thumb,
.releases-history::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 3px;
}
